//Vue-Router 관련 설정
import { createWebHashHistory, createRouter} from "vue-router";

import DeveloperInfo from "../components/DeveloperInfo.vue";
import QuestionList from "../components/QuestionList.vue";
import QuestionRegist from "../components/QuestionRegist.vue";

// const router = createRouter({옵션})
const router = createRouter({
    //방문한 페이지의 이력에 대한 처리 방침
    history : createWebHashHistory(),
    //어떤 주소일 때 어떤 컴포넌트를 보여줄 지 설정
    routes:[
        {path : "/" , component : DeveloperInfo },
        {path : "/list", component : QuestionList },
        {path : "/regist", component : QuestionRegist},
    ],

});

export default router;//완성된 객체;