<template>
<!-- 화면에 보여줄 HTML 요소를 구현 -->
<div class="row mt-4">
    <div class="col-md-8 offset-md-2">
        <div class="p-4 text-light bg-dark rounded">
            <h1>휴대폰 목록</h1>
        </div>
    </div>
</div>

<div class="row mt-4">
    <div class="col-md-8 offset-md-2">
        <ul class="list-group">
            <li class="list-group-item" v-for="(p, i) in productList" v-bind:key="i">
                <div @click="toggle(i);">{{p.title}}</div>
                <div v-if="p.expand">{{p.description}}</div>
                <div class="d-flex" v-if="p.expand">
                    <div style="width:20%" class="p-1" v-for="(img, index) in p.images" v-bind:key="index">
                        <img v-bind:src="img" style="width:100%;" class="img-thumbnail">
                    </div>
                </div>
            </li>
        </ul>
    </div>
</div>

</template>
<script>
//화면에 연결하여 처리할 요소를 구현(프로그래밍)
import axios from "axios";

export default {
    name:"QuestionList",//외부에서 나를 불러가면 QuestionList라는 이름으로 써야한다!
    data(){
        return {
            productList:[],
        };
    },
    computed:{},
    methods:{
        toggle(index){
            if(!this.productList[index].expand)//없으면
                this.productList[index].expand = true;
            else
                this.productList[index].expand = false;
        },
    },
    created(){
        axios.get("https://dummyjson.com/products")
        .then(resp=>{
            this.productList = resp.data.products;
        });
    },
};
</script>
<style scoped>
/* 화면에 연결하여 처리할 요소를 구현(디자인). scoped를 추가하면 내부적으로만 적용 */
</style>